:root {
  --accent-color: #d41443;
  --first-color-text: #fff;
  --secondary-color-text: #ffa5ba;
  --third-color-text: #000;
  --forth-color-text: #907e82;
  --background-color: #e17992;
  --secondary-bg-color: #ffb8ca;
  --third-bg-color: #f0d1a5;
  --forth-bg-color: #c2e297;
  --footer-bg-color: #fff5f6;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --duration: 300ms;
  --main-font: "DM Sans", sans-serif;
  --secondary-font: "Titan One", sans-serif;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
