}

.alert--warning {
  color: #fc0909;
}

.alert-icon {
  fill: currentColor;
  opacity: 0;
  transition: opacity .3s ease-in-out .1s, transform .3s ease-in-out .1s;
  display: inline-flex;
  position: absolute;
  bottom: 7px;
  right: 10px;
  transform: scale(.3);
}

